#consulting-div{
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  margin-top: 50px;
}

.consulting-box{
  width: auto;
  height: auto;
  border-top: 1px solid #0061E0;
}

.consulting-box1{
  width: auto;
  height: auto;
  border-top: 1px solid #0061E0;
}

.consulting-title{
  margin-top: 25px;
  text-align: center;
  color: #000;
  font-size: 32px;
  font-weight: 800;
}

.consulting-price{
  font-size: 34px;
  font-weight: 800;
  color: #000;
  text-align: center;
  margin-bottom: 90px;
}

.consulting-text{
  text-align: center;
  color: black;
  font-size: 14px;
  font-weight: 600;
}

.consulting-title-text{
  text-align: center;
  margin-top: 15px;
  margin-bottom: 60px;
  color: #0061E0;
  font-size: 12px;
  font-weight: 600;
}

.consulting-step{
  font-size: 18px;
  font-weight: 700;
  color: #0061e0;
}

.consulting-butt{
  width: 200px;
  height: 40px;
  border: 0;
  border-radius: 999px;
  display: block;
  margin: 0 auto;
  background-color: #0061E0;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 100px;
}

.consulting-icon{
  width: auto;
  height: 43px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.consulting-icon-1{
  width: auto;
  height: 13px;
  margin: 0 auto;
}

#last-size{
  margin-bottom: 30px;
}

.consulting-strong{
  font-size: 18px;
  margin-left: 5px;
}

@media screen and (max-width: 720px) {

  #consulting-div{
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
    margin-top: 50px;
    margin-left: 20px;
    margin-right: 20px;
  }  
}